<template>
    <div class="safety" v-if="safetySetting">
        <div class="safety-info">
            <div class="safety-logo">
                <user-avatars class="img" :src="safetySetting.head"></user-avatars>
            </div>
            <div class="safety-text">
                <p><span class="tit">{{$L('会员账号')}}：</span>{{ safetySetting.username }}</p>
                <p><span class="tit">{{$L('会员昵称')}}：</span>{{ safetySetting.nickname }}</p>
                <p><span class="tit">{{$L('最近登录时间')}}：</span>{{ safetySetting.loginTime }}</p>
            </div>
        </div>
        <div class="safetyLevel">
            <div class="safety-progress">
                <span class="tit">{{$L("安全等级")}}</span>
                <el-progress color="mainBgColor" :show-text="false" :stroke-width="20" :percentage="safetySetting.percent"></el-progress>
                <span class="size mainFontColor">{{gradeFunc(safetySetting.grade)}}</span>
            </div>
            <table class="table">
                <tr class="tr" v-if="safetySetting.password && safetySetting.password.hasOwnProperty('isset')">
                    <td class="td" style="width: 160px"><i class="bbx-font bbx-icon-denglumima-" style="color: #72ADEC;"></i></td>
                    <td class="td" style="width: 160px">{{$L("登录密码")}}</td>
                    <td class="td" >{{$L("安全性高的密码可以使帐号更安全。建议您定期更换密码，设置一个6-20位包含字母数字组合的密码")}}</td>
                    <td class="td" style="width: 300px">
                        <p v-if="safetySetting.password.isset === 1">
                            <span class="green span mainFontColor">{{$L("已设置")}}</span> | <span class="span hand mainFontColor" @click="modifyPwd">{{$L("修改")}}</span>
                        </p>
                        <p v-else-if="safetySetting.password.isset === 0">
                            <span class="red span mainFontColor">{{$L("未设置")}}</span> | <span class="span hand mainFontColor" @click="setPwd">{{$L("设置")}}</span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('email')">
                    <td class="td"><i class="bbx-font bbx-icon-youxiangbangding-" style="color: #1EC5A1;"></i></td>
                    <td class="td">{{$L("邮箱绑定")}}</td>
                    <td class="td">{{$L("保障账号安全，忘记密码轻松找回密码")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.email.isset === 1">
                            <span class="green span">{{$L("已绑定")}}{{hidePhone(safetySetting.email.emailNum)}}</span> | <span class="span hand mainFontColor" @click="modifyEmail">{{$L("修改")}}</span>
                        </p>
                        <p v-else-if="safetySetting.email.isset === 0">
                            <span class="red span">{{$L("未绑定")}}</span> | <span class="span hand mainFontColor" @click="setEmail">{{$L("绑定")}}</span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('mobile')">
                    <td class="td"><i class="bbx-font bbx-icon-shoujibangding-" style="color: #FFC571;"></i></td>
                    <td class="td">{{$L("手机绑定")}}</td>
                    <td class="td">{{$L("保障账号安全，忘记密码轻松找回密码，还可享受手机快捷登录")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.mobile.isset === 1">
                            <span class="green span">{{$L("已绑定")}}{{hidePhone(safetySetting.mobile.mobileNum)}}</span> | <span class="span hand mainFontColor" @click="modifyPhone">{{$L("修改")}}</span>
                        </p>
                        <p v-else-if="safetySetting.mobile.isset === 0">
                            <span class="red span">{{$L("未绑定")}}</span> | <span class="span hand mainFontColor" @click="setPhone">{{$L("绑定")}}</span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('wx') && safetySetting.wx.hasOwnProperty('isset')">
                    <td class="td"><i class="bbx-font bbx-icon-guanlianweixinzhanghao-" style="color: #38BA35;"></i></td>
                    <td class="td">{{$L("关联微信账号")}}</td>
                    <td class="td">{{$L("关联账号后，可通过关联账号快捷登录")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.wx.isset === 1">
                            <span class="green span">{{$L("已关联")}}</span> | <span class="span hand mainFontColor" @click="unBindWeChat">{{$L("解绑")}}</span>
                        </p>
                        <p v-else-if="safetySetting.wx.isset === 0">
                            <span class="red span">{{$L("未关联")}}</span> | <span class="span hand mainFontColor" @click="bindWeChat">{{$L("关联")}}</span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('qq') && safetySetting.qq.hasOwnProperty('isset')">
                    <td class="td"><i class="bbx-font bbx-icon-guanliQQzhanghao-" style="color: #108EE9;"></i></td>
                    <td class="td">{{$L("关联QQ账号")}}</td>
                    <td class="td">{{$L("关联账号后，可通过关联账号快捷登录")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.qq.isset === 1">
                            <span class="green span">{{$L("已关联")}}</span> | <span class="span hand mainFontColor" @click="untiedQQ">{{$L("解绑")}}</span>
                        </p>
                        <p v-else-if="safetySetting.qq.isset === 0">
                            <span class="red span">{{$L("未关联")}}</span> | <span class="span hand mainFontColor" @click="bindQQ">{{$L("关联")}}</span>
                        </p>
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import bindPhone from '../../../components/account/bindPhone.vue';
    import bindPassword from '../../../components/account/bindPassword.vue';
    import bindEmail from '../../../components/account/bindEmail.vue';
    import bindQQ from '../../../components/account/bindQQ.vue';
    import bindWeChat from '../../../components/account/bindWeChat.vue';
    import userAvatars from '../../../components/common/userAvatars.vue';
    export default {
        name: "safety",
        components:{
            bindPhone,bindPassword,bindEmail,userAvatars,bindQQ,bindWeChat
        },
        data(){
            return {
                safetySetting:{}
            }
        },
        created () {
            this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('safetySetting',{}, function (reData) {
                    if (reData.flag === 1) {
                        _self.safetySetting = reData.data;
                        _self.loading = false;
                        _self.percentFunc();
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.loading = false;
                            _self.showError(_self.$L('登录超时'),_self.$L('请重新登录'),'error');
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action === 'cancel') {
                                _self.$router.push({name: 'home'});
                            } else {
                                _self.loading = false;
                                _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                            }
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                        }
                    });
                });
            },
            gradeFunc(g){
                if(g === 1){
                    return this.$L('低');
                }else if(g === 2){
                    return this.$L('中');
                }else if(g === 3){
                    return this.$L('高');
                }
            },
            percentFunc(){
                const _self = this;
                let i = 0;
                let count = 0;
                if (_self.safetySetting.hasOwnProperty('password')) {
                    count++;
                    if(_self.safetySetting.password.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('email')) {
                    count++;
                    if(_self.safetySetting.email.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('mobile')) {
                    count++;
                    if(_self.safetySetting.mobile.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('wx')) {
                    count++;
                    if(_self.safetySetting.wx.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('qq')) {
                    count++;
                    if(_self.safetySetting.qq.isset === 1) i++;
                }
                _self.safetySetting.percent = i/count*100;
            },
            hidePhone(phone){
                return phone.substr(0, 3) + '****' + phone.substr(7);
            },
            modifyPhone(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('修改手机'),
                    view: bindPhone,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.mobile.isset,
                        mobileNum:this.safetySetting.mobile.mobileNum,
                        trueMobileNum: this.safetySetting.mobile.trueMobileNum,
                    },
                });
            },
            setPhone(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('绑定手机'),
                    view: bindPhone,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.mobile.isset,
                        mobileNum:this.safetySetting.mobile.mobileNum,
                        trueMobileNum: this.safetySetting.mobile.trueMobileNum,
                    },

                });
            },
            modifyPwd(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('修改密码'),
                    view: bindPassword,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.password.isset,
                        phoneSet:this.safetySetting.hasOwnProperty('mobile') && this.safetySetting.mobile.isset === 1 ? 1 : 0,
                    }
                });
            },
            setPwd(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('设置密码'),
                    view: bindPassword,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.password.isset,
                    }

                });
            },
            modifyEmail(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('修改邮箱'),
                    view: bindEmail,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.email.isset,
                        emailNum:this.safetySetting.email.emailNum,
                    }
                });
            },
            setEmail(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('绑定邮箱'),
                    view: bindEmail,
                    width: '500px',
                    data: {
                        isset:this.safetySetting.email.isset,
                        emailNum:this.safetySetting.email.emailNum,
                    }
                });
            },
            bindQQ(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('绑定QQ'),
                    view: bindQQ,
                    width: '815px',
                    height: '560px',
                });
            },

            untiedQQ() {
                const _self = this;
                _self.$confirm(_self.$L('是否解除绑定？'), _self.$L('解绑QQ'), {
                    confirmButtonText: _self.$L('立即解绑'),
                    cancelButtonText: _self.$L('我再想想'),
                    type: 'error',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.loading = true;
                    _self.$getApi('untiedQQ',{}, function (reData) {
                        _self.$message({
                            message: _self.$L('解绑QQ成功!'),
                            type: 'success'
                        });
                        _self.loading = false;
                        _self.getData();
                    }, function (error) {
                        if(error && error.flag == -2){
                            _self.$alert(_self.$L('尚未设置登录密码！无法解绑！'), _self.$L('解绑QQ失败!'), {
                                confirmButtonText: _self.$L('确定'),
                                type: 'error',
                                showClose: false
                            }).then(() => {
                                _self.loading = false;
                                _self.getData();
                            });
                        }else{
                            _self.$confirm(_self.$L('解绑QQ失败!'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.untiedQQ();
                            }).catch(action => {
                                _self.loading = false;
                                _self.getData();
                            });
                        }
                    });
                }).catch(action => {

                });
            },
            bindWeChat() {
                this.$store.commit('setDialogObj', {
                    name: this.$L('绑定微信'),
                    view: bindWeChat,
                    width: '815px',
                    height: '560px',
                    closeFunc:()=>{
                        this.getData();
                    }
                });
            },
            unBindWeChat() {
                const _self = this;
                _self.$confirm(_self.$L('是否解除绑定？'), _self.$L('解绑微信'), {
                    confirmButtonText: _self.$L('立即解绑'),
                    cancelButtonText: _self.$L('我再想想'),
                    type: 'error',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.loading = true;
                    setTimeout(()=>{
                        _self.$getApi('unBindWeChat',{}, function (reData) {
                            _self.$message({
                                message: _self.$L('解绑微信成功!'),
                                type: 'success'
                            });
                            _self.loading = false;
                            _self.getData();
                        }, function (error) {
                            if(error && error.flag == -2){
                                _self.$alert(_self.$L('尚未设置登录密码！无法解绑！'), _self.$L('解绑微信失败!'), {
                                    confirmButtonText: _self.$L('确定'),
                                    type: 'error',
                                    showClose: false
                                }).then(() => {
                                    _self.loading = false;
                                    _self.getData();
                                });
                            }else{
                                _self.$confirm(_self.$L('解绑微信失败!'), _self.$L('出错了'), {
                                    confirmButtonText: _self.$L('点击重试'),
                                    cancelButtonText: _self.$L('取消'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.unBindWeChat();
                                }).catch(action => {
                                    _self.loading = false;
                                    _self.getData();
                                });
                            }
                        });
                    },500);
                }).catch(action => {

                });
            }

        }
    }
</script>

<style lang="scss" scoped>

    @import "../../../assets/css/common/lib.scss";

    .safety{
        padding: 20px 20px 60px!important;
        background-color: #fff;
        width: auto!important;
        margin: 20px;
        .safety-info{
            position: relative;
            background-color: #F2F2F2;
            width: 100%;
            height: 150px;
            .safety-logo {
                top: 50%;
                position: absolute;
                transform: translateY(-50%);
                overflow: hidden;
                width: 120px;
                height: 120px;
                box-sizing: border-box;
                border-radius: 100%;
                margin-left: 20px;
                padding: 10px;
                @extend .no-select;
                .img {
                    width: 100%;
                    height: 100%;
                    border-radius: inherit;
                    background-size: 80% !important;
                    background-position: center;
                    background-repeat: no-repeat;
                    /deep/ .img-error {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                    /deep/ .el-image__inner{
                        border-radius: 50%;
                    }
                }
            }
            .safety-text{
                top: 50%;
                position: absolute;
                transform: translateY(-50%);
                font-size: 14px;
                line-height: 2.5;
                margin-left: 160px;
                .tit{
                    display: inline-block;
                    width: 100px;
                }
            }
        }
        .safetyLevel{
            padding-bottom: 20px;
            .tit{
                display: inline-block;
                width: 160px;
                text-align: center;
            }
            .safety-progress{
                font-size: 14px;
                height: 80px;
                line-height: 80px;
                .el-progress{
                    width: 50%;
                    display: inline-block;
                    vertical-align: middle;
                }
                .size{
                    padding-left: 2%;
                    font-size: 20px;
                    color: #2D8CF0;
                    vertical-align: middle;
                }
            }
            .table{
                width: 100%;
                .bbx-font{
                    font-size: 40px;
                    color: #fff;
                    vertical-align: middle;
                }
                .tr{
                    font-size: 14px;
                    border-top: 1px solid #ccc;
                    border-bottom: 1px solid #cccccc;
                    line-height: 80px;
                    .td{
                        line-height: 1.8;
                        vertical-align: middle;
                    }
                    .td:nth-child(1){
                        text-align: center;
                    }
                    .td:nth-child(2){
                        font-size: 16px;
                        min-width: 110px;
                    }
                    .td:nth-child(3){
                        color: #999999;
                    }
                    .td:nth-child(4){
                        min-width: 250px;
                        text-align: right;
                        padding-right: 2%;
                        .span{
                            padding: 0 10px;
                            color: #2D8CF0;
                        }
                        .hand{
                            cursor: pointer;
                        }
                    }
                }
                .green{
                    color: #008000!important;
                }
                .red{
                    color: #FF0000!important;
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
